<template>
  <div class="Comment">
    <SiteHeader></SiteHeader>
    <div v-if="this.not_login == false">
      <el-container style="border: 1px solid #eee">
        <Sidebar></Sidebar>
        <el-container>
          <el-header>
            <i class="el-icon-chat-line-round"></i>
          </el-header>

          <el-main style="text-align: left">
            <el-row>
              <el-col :span=8>
                <el-input placeholder="! ~" v-model="searchText" clearable prefix-icon="el-icon-search"></el-input>
              </el-col>
              <el-col :span=2>
                <el-button type="primary" icon="el-icon-search" @click="searchComments();">
                </el-button>
              </el-col>
              <el-col :span=6>
                <el-button type="info" icon="el-icon-edit"
                @click="dialogFormVisible=true"></el-button>
                <i class="el-icon-back"></i>
                <span><el-tag>?</el-tag></span>
              </el-col>
            </el-row>

            <el-row>
              <el-divider></el-divider>
            </el-row>

            <el-table
            :data="comments"
            border
            :default-sort = "{prop: 'p_rate', order: 'descending'}"
            style="width: 80%">
              <el-table-column
                fixed
                prop="p_cid"
                label=""
                width="150">
              </el-table-column>
              <el-table-column
                prop="p_coursename"
                label=""
                width="200">
              </el-table-column>
              <el-table-column
                prop="p_rate"
                label=""
                sortable
                width="120">
              </el-table-column>
              <el-table-column
                prop="p_comment"
                label=""
                width="120">
              </el-table-column>
              <el-table-column
                label=""
                width="240">
                <template slot-scope="scope">
                  <el-button @click="handleViewClick(scope.row)" type="primary" size="small"></el-button>
                  <el-button @click="handleEditClick(scope.row)" type="warning" size="small"></el-button>
                  <el-button @click="handleDeleteClick(scope.row)" type="danger" size="small"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-main>

          <el-footer>
            <el-pagination
            background
            layout="prev, pager, next"
            :total.sync="maxPage"
            :current-page.sync="currentPage"
            @current-change="handleCurrentChange">
            </el-pagination>
          </el-footer>
        </el-container>

      </el-container>
      <!-- Magic Don't touch -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="CommentForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-autocomplete v-model="CommentForm.name" :fetch-suggestions="querySearchAsync"></el-autocomplete>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-rate v-model="CommentForm.rate" show-text></el-rate>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input type="textarea" :rows="2"
            v-model="CommentForm.text" maxlength="200" show-word-limit></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible=false; resetForm();"> </el-button>
          <el-button type="primary" @click="addComments();dialogFormVisible=false"> </el-button>
        </div>
      </el-dialog>

      <el-dialog title="" :visible.sync="CommentDetailFormVisible">
        <el-form :model="CommentDetailForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="CommentDetailForm.user" autocomplete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="CommentDetailForm.name" autocomplete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="CommentDetailForm.coursename" autocomplete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-rate v-model="CommentDetailForm.rate" show-text :disabled="true"></el-rate>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input type="textarea" :rows="2"
            v-model="CommentDetailForm.text" :disabled="true"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="CommentDetailFormVisible=false"></el-button>
        </div>
      </el-dialog>

      <el-dialog title="" :visible.sync="editFormVisible">
        <el-form :model="EditCommentForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="EditCommentForm.name" autocomplete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-rate v-model="EditCommentForm.rate" show-text></el-rate>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input type="textarea" :rows="2"
            v-model="EditCommentForm.text" maxlength="200" show-word-limit></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer">
          <el-button @click="editFormVisible=false; resetForm();"> </el-button>
          <el-button type="primary" @click="EditedFormSubmit();editFormVisible=false;"> </el-button>
        </div>
      </el-dialog>

      <el-drawer title="" :visible.sync="drawer" :with-header="true" size="50%">
        <el-table :data="resultForms">
            <el-table-column fixed prop="p_cid" label="" width="150">
            </el-table-column>
            <el-table-column prop="p_rate" label="" width="120">
            </el-table-column>
            <el-table-column prop="p_comment" label="" width="120">
            </el-table-column>
            <el-table-column
              label=""
              width="240">
              <template slot-scope="scope">
                <el-button @click="handleViewClick(scope.row)" type="primary" size="small"></el-button>
                <el-button @click="handleEditClick(scope.row)" type="warning" size="small"></el-button>
                <el-button @click="handleDeleteClick(scope.row)" type="danger" size="small"></el-button>
              </template>
            </el-table-column>
        </el-table>
        <el-footer>
          <el-pagination
          background
          layout="prev, pager, next"
          :total.sync="maxCommentPage"
          :current-page.sync="currentCommentPage"
          @current-change="handleCurrentCommentChange">
          </el-pagination>
          </el-footer>
      </el-drawer>

    </div>
    <div v-else>
      <el-page-header @back="goBack" content="404">
      </el-page-header>
      <p>Not Logged in! !!</p>
    </div>
  </div>
</template>

<script>
import SiteHeader from '@/components/SiteHeader'
import axios from 'axios'
import Sidebar from '@/components/Sidebar'

export default {
  name: 'Comment',
  components: {SiteHeader, Sidebar},
  inject: ['reload'],
  data () {
    return {
      count: 0,
      comments: [], // ,
      not_login: false,
      searchText: '',
      baseSearchUrl: '',
      currentPage: 0, // 
      maxPage: 100, // 
      userEdit: '',
      userDelete: '',
      timeout: null,
      cid_list: [],
      CommentForm: { // 
        name: '',
        rate: 0,
        text: ''
      },
      EditCommentForm: { // 
        p_id: 0,
        name: '',
        rate: 0,
        text: ''
      },
      // 
      CommentDetailForm: {
        user: '',
        name: '',
        coursename: '',
        rate: 0,
        text: ''
      },
      dialogFormVisible: false,
      editFormVisible: false,
      CommentDetailFormVisible: false,
      formLabelWidth: '120px',
      drawer: false,
      resultForms: [], // 
      currentCommentPage: 0, // 
      maxCommentPage: 100 // 
    }
  },
  methods: {
    goBack () {
      this.$router.push({name: 'Index'})
    },
    decodeError (msg) {
      let pat = /[0-9]{3}/
      let code = pat.exec(msg)[0]
      let res = ''
      switch (code) {
        case '401':
          res = ', ! /!'
          break
        case '403':
          res = ', !'
          break
        case '404':
          res = ', !'
          break
        case '500':
          res = '!'
          break
        default:
          res = ''
      }
      return res
    },
    handleCurrentChange () {
      this.showComments()
    },
    handleCurrentCommentChange () {
      const that = this
      const page = that.currentCommentPage
      let url = that.baseSearchUrl
      if (page !== 1 && page !== 0) {
        url = url + '&page=' + page
      }
      console.log(url)
      axios.get(url).then(function (response) {
        that.resultForms = response.data.results
      }).catch(function (error) {
        that.$notify({
          title: '',
          message: that.decodeError(error.message),
          type: 'error'
        })
      })
    },
    //  
    showComments () {
      // TODO fetch data from backend
      const that = this
      const page = that.currentPage
      let url = 'http://localhost:8000/api/comments'
      if (page !== 1 && page !== 0) {
        url = url + '/?page=' + page
      } else {
        url = url + '/'
      }
      axios.get(url).then(function (response) {
        that.comments = response.data.results
        that.maxPage = (Math.ceil(response.data.count / 10)) * 10
      }).catch(function (error) {
        that.$notify({
          title: '',
          message: that.decodeError(error.message),
          type: 'error'
        })
      })
    },
    //  
    addComments () {
      const that = this
      const token = localStorage.getItem('access.mysite')
      axios.post('http://localhost:8000/api/comments/', {
        p_cid: that.CommentForm.name,
        p_rate: that.CommentForm.rate * 20,
        p_comment: that.CommentForm.text,
        p_owner: localStorage.getItem('username.mysite')
      }, {
        headers: {Authorization: 'Bearer ' + token}
      }).then(function (response) {
        // , 
        that.$notify({
          title: '',
          message: ',~',
          type: 'success'
        })
      }).catch(function (error) {
        that.$notify({
          title: '',
          message: that.decodeError(error.message),
          type: 'warning'
        })
      })
    },
    // OK
    searchComments () {
      let ans = this.getSearchCount()
      const that = this
      that.drawer = true
      ans.then(() => {
        // console.log(that.baseSearchUrl) // url
        // console.log(that.maxCommentPage) // 
        if (that.baseSearchUrl) {
          that.baseSearchUrl = that.baseSearchUrl.slice(0, -7)
        }
        // Fetch the first page
        axios.get('http://localhost:8000/api/comments/', {
          params: {
            p_cid: that.searchText
          }
        }).then(function (response) {
          that.resultForms = response.data.results
          that.$notify({
            title: '',
            message: that.searchText,
            type: 'success'
          })
        }).catch(function (error) {
          console.log(error)
          that.$notify({
            title: '',
            message: that.decodeError(error.message),
            type: 'danger'
          })
        })
      })
    },
    getUserInfo () {
      const that = this
      const storage = localStorage
      that.not_login = (storage.getItem('username.mysite') === null)
    },
    // OK
    handleViewClick (row) {
      const that = this
      that.getCommentUser(row.p_id).then((response) => {
        if (!response.data.count) {
          that.CommentDetailForm.user = 'Unknown'
        } else {
          that.CommentDetailForm.user = response.data.results[0].m_username
        }
        that.CommentDetailForm.name = row.p_cid
        that.CommentDetailForm.rate = row.p_rate / 20
        that.CommentDetailForm.text = row.p_comment
        that.CommentDetailForm.coursename = row.p_coursename
      }).catch((error) => {
        that.$notify({
          title: '',
          message: that.decodeError(error.message),
          type: 'error'
        })
      })
      that.CommentDetailFormVisible = true
    },
    // OK
    handleEditClick (row) {
      const curUser = localStorage.getItem('username.mysite')
      const that = this
      that.getCommentUser(row.p_id).then((response) => {
        if (!response.data.count) {
          that.userEdit = 'Unknown'
        } else {
          that.userEdit = response.data.results[0].m_username
        }

        if (curUser !== that.userEdit) {
          that.$notify({
            title: '',
            message: '',
            type: 'error'
          })
        } else {
          that.EditCommentForm.p_id = row.p_id
          that.EditCommentForm.name = row.p_cid
          that.editFormVisible = true
        }
      }).catch((error) => {
        that.$notify({
          title: '',
          message: that.decodeError(error.message),
          type: 'error'
        })
      })
    },
    // OK
    handleDeleteClick (row) {
      const curUser = localStorage.getItem('username.mysite')
      const that = this
      const token = localStorage.getItem('access.mysite')
      that.getCommentUser(row.p_id).then((response) => {
        if (!response.data.count) {
          that.userDelete = 'Unknown'
        } else {
          that.userDelete = response.data.results[0].m_username
        }
        if (curUser !== that.userDelete) {
          this.$notify({
            title: '',
            message: '!',
            type: 'error'
          })
        } else {
          that.$confirm(',?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            axios.delete('http://localhost:8000/api/comments/' +
            row.p_id + '/', {
              params: {
                p_cid: row.p_cid,
                p_rate: row.p_rate,
                p_owner: row.p_owner
              },
              headers: {Authorization: 'Bearer ' + token}
            }).then((result) => {
              that.$notify({
                title: '!',
                message: '',
                type: 'success'
              })
            }).catch((error) => {
              that.$notify({
                title: '',
                message: that.decodeError(error.message),
                type: 'warning'
              })
            })
          })
        }
      })
    },
    // OK
    EditedFormSubmit () {
      const that = this
      const token = localStorage.getItem('access.mysite')
      axios.put('http://localhost:8000/api/comments/' +
        that.EditCommentForm.p_id + '/', {
        p_cid: that.EditCommentForm.name,
        p_rate: that.EditCommentForm.rate * 20,
        p_comment: that.EditCommentForm.text
      }, {
        headers: {Authorization: 'Bearer ' + token}
      }).then((result) => {
        that.$notify({
          title: '',
          message: '',
          type: 'success'
        })
      }).catch((error) => {
        that.$notify({
          title: '',
          message: that.decodeError(error.message),
          type: 'error'
        })
      })
    },
    // OK
    resetForm () {
      this.$notify({
        title: '',
        message: 'wow ~',
        type: 'success'
      })
    },
    createStateFilter (queryString) {
      return (state) => {
        return (state.value.indexOf(queryString) === 0)
      }
    },
    querySearchAsync (queryString, cb) {
      let n = this.comments.length
      let cid = new Array(n)
      for (let i = 0; i < n; i++) {
        if (this.comments[i]) {
          cid[i] = {'value': this.comments[i].p_cid}
        }
      }
      this.cid_list = cid
      // console.log(this.cid_list)
      let results = queryString ? this.cid_list.filter(this.createStateFilter(queryString)) : this.cid_list
      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 3000 * Math.random())
    },
    // OK
    async getCommentUser (commentId) {
      let ans
      try {
        let response = await axios.get('http://localhost:8000/api/make_comment/', {
          params: {
            m_commentid: commentId
          }
        })
        ans = response
      } catch (error) {
        console.log(error.message)
      }
      return ans
    },
    async getSearchCount () {
      const that = this
      try {
        let response = await axios.get('http://localhost:8000/api/comments/', {
          params: {
            p_cid: that.searchText
          }
        })
        that.maxCommentPage = (Math.ceil(response.data.count / 10)) * 10
        that.baseSearchUrl = response.data.next
      } catch (error) {
        console.log(error.message)
      }
    }
  },
  created: function () {
    this.getUserInfo()
    setTimeout(this.showComments(), 1000)
  }
}
</script>

<style type="text/css">
.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

.box-card {
  width: 480px;
}
.el-header {
  background-color: #B3C0D1;
  color: #333;
  line-height: 1.7;
  font-size: 25px;
  text-align: left;
}

</style>



